import axios from "axios"
import URLS from "../Helpers/URLS"
    
export function atender(dni, token){
    axios.delete(`${URLS.MAIN_BACKEND}/api/turnos/eliminar/${dni}`,{headers:{
        'x-token':token
    }})
    .then(res=>{
        if(res.data.error){
            return true
        }else{
            return false    
        }
        
    })
    .catch(err=>{
        console.log(err)
        return true
    })
}