import { useEffect, useState } from "react";
import { Alert } from "react-bootstrap"
import { useDispatch } from "react-redux";

export function DescribeErrors({data}){
    const distpatch = useDispatch();
    if(data.length === 0){
        return <></>
    }else{
        return(
            <Alert variant="danger" onClose={(e) => distpatch({type:'ERROR', payload: []})} dismissible>
                <Alert.Heading>¡ERROR!</Alert.Heading>
                <ul>
                    {data.map((err, err_i)=>{
                        return <li key={err_i}>{err.toString()}</li>
                    })}
                </ul>
            </Alert>
        )
    }
}
