import { Navbar, Container, Nav, NavDropdown, Offcanvas, Button } from "react-bootstrap";
import { Link, NavLink} from "react-router-dom";
import { useEffect, useState } from "react";
import Menu from '../Helpers/Menu.js';
import hospital from '../Img/svg/hospital-stop-bg.svg';
import { useDispatch } from "react-redux";

export default function CustomNavbar(){
    const [show, setShow] = useState(false);
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);
    const [title, setTitle] = useState('Tu Turno');
    const dispatch = useDispatch();

    //const validate1 = useValidate(0);
    
    useEffect(()=>{
        /*validate1.check().then(res=>{
                if(!res.data.error){
                    dispatch({type:'IS_LOGGED'});
                }else{
                    dispatch({type:'IS_LOGGEDOUT'});
                }
            }
        ).catch(err=>{
            dispatch({type:'IS_LOGGEDOUT'});
        })*/
        document.title = title;
    },[title])

    return(
    <>
        <Navbar bg="dark" className="navbar-dark" expand="lg">
          <Container>
            <Navbar.Brand>Tu Turno</Navbar.Brand>
            <Button className="d-lg-none" variant="secondary" onClick={handleShow}><span className="menu-button">≡</span></Button>            
            <Navbar.Collapse id="basic-navbar-nav">
              <Nav className="me-auto enlaces">
                {Menu.map((_link, _index)=>{
                    if(!_link.dropdown){
                        return  <NavLink onClick={()=>setTitle(_link.alias)} key={_index} to={_link.link} className={({isActive})=> isActive ? 'nav-link link-activo' : 'nav-link'}>{_link.alias}</NavLink>
                    }else{
                        return <NavDropdown title={_link.alias} id="basic-nav-dropdown" key={_index}>{_link.sub_links.map((_sub, _indx)=>{
                            if(_sub.divider){
                                return <>
                                    <NavDropdown.Item key={_indx}>
                                        <Link to={_sub.link}>{_sub.alias}</Link>
                                    </NavDropdown.Item>
                                    <NavDropdown.Divider />
                                </>
                            }else{
                                return <NavDropdown.Item  key={_indx}>
                                        <Link  to={_sub.link}>{_sub.alias}</Link>
                                    </NavDropdown.Item>
                            }                            
                        })}</NavDropdown>
                    }                    
                })}
              </Nav>
            </Navbar.Collapse>
          </Container>
        </Navbar>
        <Offcanvas show={show} onHide={handleClose} className="bg-dark w-50 enlaces">
            <div>
                <img src={hospital} alt="" width="100%" id="foto_perfil"/>
            </div>
            <Offcanvas.Header>
            <Offcanvas.Title className="text-white">Tu Turno</Offcanvas.Title>
            </Offcanvas.Header>

            <Offcanvas.Body>
                <div>
                {Menu.map((_link, _index)=>{
                    if(!_link.dropdown){
                        return <NavLink onClick={()=>setTitle(_link.alias)} key={_index} to={_link.link} className={({isActive})=> isActive ? 'nav-link link-activo' : 'nav-link'}>{_link.alias}</NavLink>
                    }else{
                        return <NavDropdown title={_link.alias} id="basic-nav-dropdown" key={_index}>{_link.sub_links.map((_sub, _indx)=>{
                            if(_sub.divider){
                                return <>
                                    <NavDropdown.Item key={_indx}>
                                        <Link to={_sub.link}>{_sub.alias}</Link>
                                    </NavDropdown.Item>
                                    <NavDropdown.Divider />
                                </>
                            }else{
                                return <NavDropdown.Item  key={_indx}>
                                        <Link to={_sub.link}>{_sub.alias}</Link>
                                    </NavDropdown.Item>
                            }                            
                        })}</NavDropdown>
                    }                    
                })}
                </div>
            </Offcanvas.Body>          
        </Offcanvas>
    </>
    )
}

