//import { useDispatch, useSelector } from "react-redux";
import Login from "./Components/Login";
import 'bootstrap/dist/css/bootstrap.min.css';
import TablaDinamica from "./Components/TablaDinamica";
import RegistroPaciente from "./Pages/RegistroPaciente";
import TurnoActual from "./Components/TurnoActual";
import qrIndex from './Img/qr-index.jpg';
import { useEffect, useState } from "react";
import axios from "axios";
import URLS from "./Helpers/URLS";
import CustomNavbar from "./Components/CustomNavbar";
import { BrowserRouter as Router, Route, Routes, useLocation } from "react-router-dom";
import { io } from "socket.io-client";
import CrudTurnos from "./Components/CrudTurnos";
import { useDispatch, useSelector } from "react-redux";
import QRCode from 'react-qr-code';
import MiTurno from "./Pages/MiTurno";
import { Toast } from "./Components/Toast";
import { Configuracion } from "./Pages/Configuracion";
import { Notificarme, VerificarSuscripcion } from "./Helpers/Notificarme";

const socket = io(URLS.IP_BACKEND_IO+URLS.PORT_SOCKET_IO);

function App() {
  const isLogged = useSelector((state)=>state.login)
  //const errors = useSelector((state)=>state.errors)
  const theme = useSelector((state)=>state.theme)
  //const next = useSelector((state)=>state.next)
  const dispatch = useDispatch()
  const [turnos, setTurnos] = useState([])
  const [primerosTres, setPrimerosTres] = useState([])
  let paciente = localStorage.getItem('paciente') || null;
  let user = localStorage.getItem('user') || null;
  let token = '';
  const [faltan, setFaltan] = useState(0);
  const location = useLocation();
  const [logueado, setLogeado] = useState(false);
  const [doctor, setDoctor] = useState(false);

  if(user){
    token = JSON.parse(user).token;
    dispatch({type:'CURRENT', payload: JSON.parse(user)})
  }else if(paciente){
    token = JSON.parse(paciente).token;
    dispatch({type:'CURRENT', payload: JSON.parse(paciente)})
  }

  useEffect(()=>{
    document.body.classList.add(theme)
    //if ( navigator.serviceWorker ) {
    //  navigator.serviceWorker.register('/sw.js').then((reg)=>{
    //    swReg = reg;
    //    swReg.pushManager.getSubscription().then(res=>console.log(res));
    //  });
    //}
    //swReg.pushManager.getSubscription().then( verificaSuscripcion );

    function verificaSuscripcion( activadas ) {  
      if ( activadas ) {
        console.log('suscripcion activada')
      } else {
        console.log('suscripcion NO activada')
      }
    }

    socket.on('pacientes',(args = [])=>{      
      if(paciente !== null){
        if(JSON.parse(args).some(e=>e.dni === JSON.parse(paciente).dni_cuil) === false){
          localStorage.removeItem('paciente');
        }
      }
      setTurnos(JSON.parse(args));
      porDelante(JSON.parse(args), JSON.parse(paciente));
    })
    axios.get(`${URLS.MAIN_BACKEND}/api/turnos`, {timeout:1000, headers:{'x-token': token}}).then(res=>{
      setTurnos(res.data);
      porDelante(res.data, JSON.parse(paciente));
    })
    .catch(err=>{console.log('Sin permiso')})

    socket.on('primeros-tres',(args)=>{
      setPrimerosTres(JSON.parse(args));
    })
    axios.get(`${URLS.MAIN_BACKEND}/api/turnos/primeros-tres`, {timeout:1000, headers:{'x-token': token}}).then(res=>setPrimerosTres(res.data))
    .catch(err=>{console.log('Sin permiso')})
    
  }, [])
  useEffect(()=>{
    axios.get(`${URLS.MAIN_BACKEND}/api/auth/token`,{timeout:1000, headers:{'x-token': token}})
    .then(res=>{
      if(!res.data.error){
          if(res.data.type === 'paciente'){
            setLogeado(true);
          }else if(res.data.type === 'admin'){
            setLogeado(true);
            setDoctor(true);
          }          
      }else{
          setLogeado(false);
          localStorage.removeItem('status'); localStorage.removeItem('user');
          localStorage.removeItem('paciente'); localStorage.removeItem('tema');
      }
    }).catch(err=>{
      setLogeado(false);
      localStorage.removeItem('status'); localStorage.removeItem('user');
      localStorage.removeItem('paciente'); localStorage.removeItem('tema');
    })
  },[location.pathname])

  function porDelante(turnos = [], actual = ''){
    let cont = -1;
    if(actual !== null){
      let turno = turnos.find(e=>e.dni === actual.dni_cuil)
      if(turno !== undefined){
        for(let c = 0; c < turnos.length; c++){
          if(turnos[c].mesa === turno.mesa){
            cont++;
            if(turnos[c].dni === turno.dni){
              break;
            }
          }
        }
        /*if(cont <= 3){
          new Notification(`Faltan ${cont} pacientes para tu turno`)
        }*/
        setFaltan(cont);
      }else{
        setFaltan(0);
      }
    }else{
      setFaltan(0);
    }
  }
  //recordar mover el NAVBAR de lugar una vez empecemos a configurar el Custom Router (Componentes basicos en la clases)
  return (
    <>
      <CustomNavbar/>
      <Toast BG_COLOR='' TXT_COLOR='' SITE='bottom' TIME={1000}></Toast>
        <section className={`App container-fluid ${theme} pt-2`}>
          <Routes>
            <Route exact path="/" element={
              <div className="row slideOut">
                <TablaDinamica DATA={turnos || []} ACTUAL={paciente === null ? '' : JSON.parse(paciente).dni_cuil}/>
                {
                Array.isArray(primerosTres) ? primerosTres.map((tres, tres_i)=>{
                  return <TurnoActual LISTED={tres_i} key={tres_i} DATA={tres}/>
                  }) : []
                }
                {faltan !== 0 ? 
                  <article className="col-md-5 col-10 mx-auto text-center m-2 neurobox">
                  <h1>Te quedan {faltan} personas delante</h1>
                  </article>:
                  <></>
                }
                {logueado ? <></> : <div className="col-md-5 col-10 mx-auto text-center"><div width="200" className="neurobox p-2"><QRCode value={URLS.IP_FRONT+URLS.PORT_FRONTEND+'/registro'} /></div></div>}
              </div>
            }/>
            <Route exact path="/registro" element={<RegistroPaciente flag={logueado}/>}/>
            <Route exact path="/login" element={<Login flag={logueado}/>}/>
            <Route exact path="/crud-turnos" element={doctor ? <CrudTurnos DATA={turnos || []}/> : <div className="alert alert-danger slideOut">Sin permiso</div>}/>
            <Route exact path="/mi-turno" element={<MiTurno logueado={logueado} doctor={doctor} id={paciente ? paciente : user}/>}/>
            <Route exact path="/configuracion" element={logueado ? <Configuracion paciente={paciente} user={user} /> : <div className="alert alert-danger slideOut">Sin permiso, registrarse</div>}/>
          </Routes>
        </section>
    </>
  );
}

export default App;
