export function Unsubscribe(swReg){
    if(swReg){
        swReg.pushManager.getSubscription().then(subs=>{                    
            if(subs){
                subs.unsubscribe().then(()=>{
                    console.log('Notificaciones detenidas');
                })
            }else{
                console.log('ya se eliminó');
            }               
        })
    }
}