import { currentTurnReducer } from "./currentTurnReducer";
import { isLoggedReducer } from "./isLoggedReducer";
import { themeReducer } from "./themeReducer";
import { combineReducers } from "redux";
import { nextTurnReducer } from "./nextTurnReducer";
import { isErrorReducer } from "./isErrorReducer";
import { isLoadingReducer } from "./isLoadingReducer";
import { notificationReducer } from "./notificationReducer";
const allReducers = combineReducers({
    current: currentTurnReducer,
    next: nextTurnReducer,
    login: isLoggedReducer,
    theme: themeReducer,
    errors: isErrorReducer,
    loading: isLoadingReducer,
    notificacion: notificationReducer
})

export default allReducers;