export function UserAgent(user){
    switch(user){
        case /Android/i.test(user):
            return 'Android'
        case /webOS/i.test(user):
            return 'Android'
        case /iPhone/i.test(user):
            return 'Android'
        case /iPad/i.test(user):
            return 'Android'
        case /iPod/i.test(user):
            return 'Android'
        case /BlackBerry/i.test(user):
            return 'Android'
        case /IEMobile/i.test(user):
            return 'Android'
        case /Opera Mini/i.test(user):
                return 'Android'
        case /Windows/i.test(user):
            return 'Android'
        default:
            return 'Windows'
    }
}