export default function TablaDinamica({DATA, ACTUAL}){//ESTA QUEDA COMO ESTA, LUEGO HAREMOS UNA FUNCIONALIDAD PARA CRUD DINAMICO, Y PARA FILTRAR QUE CAMPOS QUEREMOS EN EL FRONT
    if(!DATA || DATA.length === 0){
        return (
            <div className="alert alert-warning">¡Sin datos!</div>
        )
    }else{

    //CAMPOS DE DATOS
    const AUX = Object.keys(DATA[0] || {});
        if(AUX.length > 0){
            const CABECERA = AUX.map(th=>{return th.toUpperCase()});
            //CAMPOS A MOSTRAR FRONTEND
            return (
                <div className="table-responsive">
                    <table className="table table-striped">
                        <thead>
                            <tr>
                                {CABECERA.map((th, th_index)=>{
                                    return (
                                        <th key={th_index}>{th.replace(/[_-]+/g, " ")}</th>
                                    )
                                })}
                            </tr>
                        </thead>
                        <tbody>
                                {DATA.map((t_row, t_row_i)=>{
                                    return <tr key={t_row_i} className={t_row.dni === ACTUAL ? 'fila-actual': ''}>{Object.values(t_row).map((t_data, t_data_i)=>{
                                        return <td key={t_data_i}>{t_data}</td>
                                    })}</tr>
                                })}
                        </tbody>
                    </table>
                </div>
            )
        }else{
            return (
                <div className="alert alert-danger">¡Sin conexion, recargar pagina, si persiste contacte con el administrador!</div>
            )   
        }
    }
}