import axios from "axios";
import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { DescribeErrors } from "./DescribeErrors";
import { Loader } from "../Helpers/Loader";
import URLS from "../Helpers/URLS";

export default function Login({flag}){
    const errors = useSelector((state)=>state.errors)
    const loading = useSelector((state)=>state.loading)
    const dispatch = useDispatch();
    if(flag){
        return (
            <div className="form col-md-6 col-12 mx-auto text-center slideOut">
                <h4>Ya estas logueado</h4>
            </div>
        )
    }else{
        return(
            <form className="form col-md-6 col-12 mx-auto text-center slideOut" onSubmit={loguear}>
                <h4>Inicio de Sesion</h4>
                {loading && <Loader width={40} color={'dodgerblue'} speed={'1s'}></Loader>}
                <DescribeErrors data={errors}></DescribeErrors>
                <input type="text" placeholder="usuario" name="usuario" className="form-control mb-1" required></input>
                <input type="password" placeholder="contra" name="contra" className="form-control mb-1" required></input>
                <button type="submit" className="btn btn-success">Ingresar</button>
            </form>
        )
    }

    async function loguear(e){
        e.preventDefault();
        dispatch({type:'LOADING'})
        let data = new FormData(e.target)

        try {
            await axios.post(URLS.MAIN_BACKEND+'/api/auth/login', {
                dni: data.get('usuario'),
                contra: data.get('contra')
            }).then(res=>{
                if(res.status === 200){
                    localStorage.setItem('user', JSON.stringify(res.data)); localStorage.removeItem('paciente');
                    window.location.reload();
                }
            })
        } catch ({response}) {
            dispatch({type:'ERROR', payload: [response.data]})
            dispatch({type:'READY'})
            dispatch({type:'IS_LOGGEDOUT'})
        }
    }
}