export function Loader({width, color, speed}){
    return(
        <svg
      xmlns="http://www.w3.org/2000/svg"
      style={{ margin: "auto" }}
      width={width}
      height={width}
      display="block"
      preserveAspectRatio="xMidYMid"
      viewBox="0 0 100 100"
    >
      <circle
        xmlns="http://www.w3.org/2000/svg"
        cx="50"
        cy="50"
        r="37"
        fill="none"
        stroke={color}
        strokeDasharray="174.35839227423352 60.119464091411174"
        strokeWidth="14"
      >
        <animateTransform
          attributeName="transform"
          dur={speed}
          keyTimes="0;1"
          repeatCount="indefinite"
          type="rotate"
          values="0 50 50;360 50 50"
        ></animateTransform>
      </circle>
    </svg>
    )
}