import axios from "axios";
import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { DescribeErrors } from "../Components/DescribeErrors";
import { Loader } from "../Helpers/Loader";
import URLS from "../Helpers/URLS";
import { UserAgent } from "../Helpers/UserAgent";

let swReg = '';
if ( navigator.serviceWorker ) {
    navigator.serviceWorker.register('/sw.js').then((reg)=>{
      swReg = reg;
    });
}

export default function RegistroPaciente({flag}){
    const isLogged = useSelector((state)=>state.login)
    const errors = useSelector((state)=>state.errors)
    const loading = useSelector((state)=>state.loading)
    const dispatch = useDispatch();
    const [doctores, setDoctores] = useState([]);

    useEffect(()=>{
        axios.get(`${URLS.MAIN_BACKEND}/api/doctores/`, {timeout:1000}).then(res=>{setDoctores(res.data)}).catch(err=>{
            dispatch({type:'ERROR', payload: [err.toString()]})
        }).catch(err=>{
            dispatch({type:'ERROR', payload: ['Error al traer datos']})
            setDoctores([]);
        })
    },[])

    if(flag){
        return (
            <div className="form col-md-6 col-12 mx-auto text-center slideOut">
            <h4>Ya estas registrado</h4></div>
        )
    }else{
        return(
            <form className="form col-md-6 col-12 mx-auto text-center slideOut" onSubmit={solicitar}>
                <h4>Solicitar Turno</h4>
                {loading && <Loader width={40} color={'dodgerblue'} speed={'1s'}></Loader>}
                <DescribeErrors data={errors}></DescribeErrors>
                <input type="number" name="dni_cuil" placeholder="dni_cuil" className="form-control mb-1" required></input>
                <input type="text" name="nombre" placeholder="nombre" className="form-control mb-1" required></input>
                <input type="text" name="apellido" placeholder="apellido" className="form-control mb-1" required></input>
                <input type="email" name="mail" placeholder="mail" className="form-control mb-1"></input>
                <input type="text" name="telefono" placeholder="telefono" className="form-control mb-1"></input>
                <select className="form-select mb-1" name="consulta" aria-label="Default select example">
                    {doctores.length > 0 ? doctores.map((doc, doc_i)=>{return <option key={doc_i} value={doc.dni_cuil}>{doc.nombre} {doc.apellido} ({doc.consulta})</option>}) : <></>}
                </select>
                <input type="hidden" name="device" value={UserAgent(window.navigator.userAgent)}></input>
                <button type="submit" className="btn btn-success">Solicitar</button>
            </form>
        )
    }

    async function solicitar(e){
        e.preventDefault();
        dispatch({type:'LOADING'})
        let data = new FormData(e.target)

        try {
            await axios.post(URLS.MAIN_BACKEND+'/api/pacientes/cargar', {
                dni_cuil: data.get('dni_cuil'),
                nombre: data.get('nombre'),
                apellido: data.get('apellido'),
                mail: data.get('mail'),
                telefono: data.get('telefono'),
                consulta: data.get('consulta'),
                device: data.get('device')
            }).then(res=>{
                if(res.status === 200){

                    if(swReg){
                        swReg.pushManager.getSubscription().then(subs=>{                    
                            if(subs){
                                subs.unsubscribe().then(()=>{
                                    alert('Notificaciones detenidas');
                                })
                            }
                        })
                    }

                    localStorage.setItem('paciente', JSON.stringify(res.data)); localStorage.removeItem('user');
                    dispatch({type:'READY'})
                    dispatch({type:'IS_LOGGED'})
                    e.target.reset();
                    window.location = '/';
                }
            })
        }catch ({response}) {
            dispatch({type:'ERROR', payload: [response.data]})
            dispatch({type:'READY'})
            dispatch({type:'IS_LOGGEDOUT'})
        }
    }
}