import { useState } from "react"
export function Toast({BG_COLOR, TXT_COLOR, TIME, SITE}){//defecto desde arriba
    const [show, setShow] = useState(true);

    SITE = SITE === 'bottom' ? {bottom:'0px'} : {top:'0px'}

    if(TIME && TIME !== 0){
        setInterval(() => {
            setShow(false)
        }, TIME);
    }

    const defaultStyles = {
        height: '3em',
        position: 'absolute',
        padding: '1em',
        width:'100%',
        display: show ? 'flex' : 'none',
        justifyContent:'center',
        alignItems:'center',
        ...SITE,
        backgroundColor: BG_COLOR ? BG_COLOR : 'dodgerblue',
        color: TXT_COLOR ? TXT_COLOR : 'white'
    }
    if(show){
        return (
            <div onClick={()=>setShow(false)} style={defaultStyles} className={SITE.top === '0px' ? 'slideFromTop' : 'slideFromBottom'}><strong>¡Notificaciones activadas!&nbsp;</strong> ,puedes desactivarlas desde el panel de configuracion.</div>
        )
    }else{
        return <></>
    }
}