import { Link, NavLink} from "react-router-dom";
import axios from "axios";
import URLS from "../Helpers/URLS";
import { useEffect } from "react";
import { Unsubscribe } from "../Helpers/Unsubscribe";

let swReg = '';

if ( navigator.serviceWorker ) {
  navigator.serviceWorker.register('/sw.js').then((reg)=>{
    swReg = reg;
  });
}


export default function MiTurno({logueado, doctor, id}){
    useEffect(()=>{
        id = JSON.parse(id);
    },[]);    
    if(doctor){
        return (
            <div className="col-md-6 col-12 mx-auto text-center slideOut">
                <h3>¿Cerrar sesion administrador?</h3>
                <button onClick={cerrarSesion} className="btn btn-danger">CERRAR</button>
            </div>
        )
    }
    if(logueado){
        return (
            <div className="col-md-6 col-12 mx-auto text-center slideOut">
                <h3>¿Cancelar turno?</h3>
                <button onClick={cerrarSesion} className="btn btn-danger">CERRAR</button>
            </div>
        )
    }else{
        return (
            <div className="col-md-6 col-12 mx-auto text-center slideOut">
                <div className="alert alert-danger">
                    <h3>Sin session</h3>
                    <p>No te has logueado, o si eres paciente no te has registrado en la cola de espera..</p>
                </div>
            </div>
        )
    }
    function cerrarSesion(){
        Unsubscribe(swReg)
        if(id){
            axios.delete(`${URLS.MAIN_BACKEND}/api/pacientes/eliminar/${id.dni_cuil}`)
            .then(res=>{
                if(!res.data.error){
                    localStorage.removeItem('user'); localStorage.removeItem('paciente'); localStorage.removeItem('tema');
                    window.location.href = '/registro';
                }else{
                    alert('Error, ¡volver a intentar!');
                }
            }).catch(err=>{
                alert('Error, ¡volver a intentar!');
            })
        }
    }
}