import axios from "axios";
import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { DescribeErrors } from "../Components/DescribeErrors";
import { GetPublicKey } from "../Helpers/GetPublicKey";
import { Loader } from "../Helpers/Loader";
import URLS from "../Helpers/URLS";

let swReg = '';
if ( navigator.serviceWorker ) {
    navigator.serviceWorker.register('/sw.js').then((reg)=>{
      swReg = reg;
      swReg.pushManager.getSubscription().then(res=>console.log);
    });
}

export function Configuracion({paciente, user}){
    const loading = useSelector((state)=>state.loading)
    const errors = useSelector((state)=>state.errors)
    const theme = useSelector((state)=>state.theme);
    const [notificaciones, setNotificaciones] = useState(false);
    const dispatch = useDispatch();
    const [persona, setPersona] = useState(paciente || user)

    useState(()=>{
        if(paciente){
            setPersona(JSON.parse(paciente));
        }else if(user){
            setPersona(JSON.parse(user));
        }

        if (!window.Notification) {
            console.log('Este navegador no soporta notificaciones');
        }
        if (Notification.permission === 'granted') {
            swReg.pushManager.getSubscription().then(subs=>{
                if(subs){
                    setNotificaciones(true);
                }else{
                    setNotificaciones(false);
                }
            })
          
        } else if (Notification.permission !== 'denied' || Notification.permission === 'default'){
            setNotificaciones(false)
        }
        setPersona(JSON.parse(persona));
    },[])

    return (
        <form className="form col-md-4 col-8 mx-auto text-center slideOut" onSubmit={configurar}>
                <h4>Configuracion</h4>
                {loading && <Loader width={40} color={'dodgerblue'} speed={'1s'}></Loader>}
                <DescribeErrors data={errors}></DescribeErrors>
                <select name="tema" defaultValue={theme} className="form-control mb-2">
                    <option value='OSCURO'>Oscuro</option><option value='CLARO'>Claro</option>
                </select>
                <select name="notificaciones" className="form-control mb-2" defaultValue={notificaciones.toString()}>
                    {notificaciones ? <><option value='true'>Activadas</option><option value='false'>Desactivadas</option></>
                     : <><option value='false'>Desactivadas</option><option value='true'>Activadas</option></>}
                </select>
                <button type="submit" className="btn btn-success" disabled={loading}>Guardar</button>
            </form>
    )/*{notificaciones ? <><option value='true'>Activadas</option><option value='false'>Desactivadas</option></> : <><option value='false'>Desactivadas</option><option value='true'>Activadas</option></>}
    <select name="notificaciones" defaultValue={notificaciones.toString()} className="form-control mb-2"></select>*/

    function configurar(e){
        e.preventDefault()
        let datos = new FormData(e.target);
        let aux_bool = datos.get('notificaciones') === 'true' ? true : false;
        localStorage.setItem('tema', datos.get('tema'));
        dispatch({type: datos.get('tema')})
        document.body.classList.replace(theme, datos.get('tema'))
        if(aux_bool){
            if ( !window.Notification ) {
                alert('Este navegador no soporta notificaciones');
            }else{
                if ( Notification.permission === 'granted' ) {        
                    swReg.pushManager.getSubscription().then(subs=>{
                        if(!subs){
                            GetPublicKey().then((key)=>{
                                swReg.pushManager.subscribe({
                                    userVisibleOnly:true,
                                    applicationServerKey:key
                                })
                                .then(res=>res.toJSON())
                                .then(subs=>{
                                    alert('Notificaciones activadas')
                                    subs.dni_cuil = persona.dni_cuil;
                                    subs.tipo = 'paciente';
                                    console.log(subs);
                                    axios.post(`${URLS.MAIN_BACKEND}/api/subscriptions/subscribe`, subs)
                                    .then(res=>{console.log(res); setNotificaciones(true); alert('Notificaciones activadas')})
                                    .catch(console.log)                                    
                                })
                            })
                        }else{
                            console.log('Ya tiene las notificaciones activadas')
                            setNotificaciones(true)
                        }
                    })
                } else if ( Notification.permission !== 'denied' || Notification.permission === 'default' )  {
                    Notification.requestPermission( function( permission ) {
                        if ( permission === 'granted' ) {
                            //dispatch({type:'NOTIFICACION', payload: aux_bool})
                            setNotificaciones(true)
                            new Notification('Notificaciones activadas');

                            GetPublicKey().then((key)=>{
                                swReg.pushManager.subscribe({
                                    userVisibleOnly:true,
                                    applicationServerKey:key
                                })
                                .then(res=>res.toJSON())
                                .then(subs=>{console.log(subs)
                                    axios.post(`${URLS.MAIN_BACKEND}/api/subscriptions/subscribe`, subs)
                                    .then(res=>{console.log(res); alert('Notificaciones activadas')})
                                    .catch(console.log)                                    
                                })
                            })
                        }
                    });
                }
            }
        }else{            
            if(swReg){
                setNotificaciones(true);
                swReg.pushManager.getSubscription().then(subs=>{                    
                    if(subs){
                        subs.unsubscribe().then(()=>{
                            alert('Notificaciones detenidas');
                        })
                    }else{
                        console.log('ya se eliminó');
                    }               
                })
            }
        }
    }
}