import axios from "axios";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { atender } from "../Helpers/CrudTurnosEventos";
import URLS from "../Helpers/URLS";
import { DescribeErrors } from "./DescribeErrors";


export default function CrudTurnos({DATA}){//ESTA QUEDA COMO ESTA, LUEGO HAREMOS UNA FUNCIONALIDAD PARA CRUD DINAMICO, Y PARA FILTRAR QUE CAMPOS QUEREMOS EN EL FRONT
    const current = useSelector((state)=>state.current);
    const errors = useSelector((state)=>state.errors);
    const dispatch = useDispatch();

    if(DATA.length === 0){
        return (
            <div className="alert alert-warning">¡Sin datos!</div>
        )
    }else{
        const AUX = Object.keys(DATA[0] || {});
        
        if(AUX.length > 0){
            //CAMPOS DE DATOS
            const CABECERA = Object.keys(DATA[0]).map(th=>{return th.toUpperCase()}); CABECERA.push('ACCIONES');
            const CABECERA_SIZE = CABECERA.length;
            //CAMPOS A MOSTRAR FRONTEND
            return (
                <div className="table-responsive slideOut">
                    <DescribeErrors data={errors}></DescribeErrors>
                    <table className="table table-striped">
                        <thead>
                            <tr>
                                {CABECERA.map((th, th_index)=>{
                                    return (
                                        <th key={th_index}>{th.replace(/[_-]+/g, " ")}</th>
                                    )
                                })}
                            </tr>
                        </thead>
                        <tbody>
                                {DATA.map((t_row, t_row_i)=>{
                                    return <tr key={t_row_i}>{Object.values(t_row).map((t_data, t_data_i)=>{
                                        if(CABECERA_SIZE - 2 === t_data_i){
                                            return (<React.Fragment key={t_data_i}>
                                                        <td>{t_data}</td>
                                                        <td><span className="btn btn-sm btn-outline-success" onClick={()=>{
                                                            let flag = window.confirm('¿Paciente atendido? - se removerá de la lista');
                                                            if(flag){
                                                                if(atender(t_row.dni, current.token)){
                                                                    dispatch({type:'ERROR', payload: ['No se pudo eliminar']})
                                                                }
                                                            }                                                        
                                                        }}>ATENDIDO ✅</span></td>
                                                    </React.Fragment>)
                                        }else{
                                            return <td key={t_data_i}>{t_data}</td>
                                        }
                                    })}</tr>
                                })}
                        </tbody>
                    </table>
                </div>
            )
        }else{
            return (
                <div className="alert alert-danger">¡Sin conexion, recargar pagina, si persiste contacte con el administrador!</div>
            )
        }
    }
}