export default function TurnoActual({DATA, LISTED}){
    const COLORS = ['#cc33ff', '#ffcc00', '#99ff99'];
    if(DATA){
        return(
            <article className="col-md-5 col-10 mx-auto text-center m-2 neurobox" style={{backgroundColor:COLORS[LISTED]}}>
                <h1>Turno {DATA.turno} - {DATA.paciente}</h1>
                <h2>Mesa {DATA.mesa} - Doctor: {DATA.doctor}</h2>
            </article>
        )
    }else{
        
    }
}